@import url(https://fonts.googleapis.com/css2?family=Eczar:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;1,300;1,400&display=swap);
.App {
  font-family: sans-serif;
}
*{
  margin:0;
}
a{
  text-decoration: none;
  color:#fff;
}
.details {
  background-color: #f8f8f8;
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-family: lato;
}
.userInfo{
  margin:10px 0 0 0;
}
.mainInfo {
  display: flex;
}
.userName {
  color: #1675B6;
}
.content{
  font-family:lato;
  font-weight: 500;
  font-size:14px;
  margin:10px;
}
.text {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
          line-clamp: 3; 
  -webkit-box-orient: vertical;
}
table {
  width: 100%;
  min-width: 200px;
}
td {
  text-align: left;
}
table,
th,
td {
  padding: 25px 10px;
  font-family:lato;
}
tr:nth-child(odd) {
  background: #f8f8f8;
}

.root {
  display: flex;
  min-height: 100px;
  align-items: center;
  width: 100%;
  justify-content: space-around;
}
.root .title {
  font-family: "Eczar";
  font-size: 1.2rem;
}
.tabs {
  display: flex;
  flex-wrap: wrap;
  font-family: "Lato";
}
.tabs h2 {
  padding: 0 10px;
  box-sizing: border-box;
}
.tabs h2:hover {
  border-bottom: 2px solid white;
}

